import { Component, OnInit, Input, Output, EventEmitter, ɵConsole } from '@angular/core';
import { ModalController, AlertController, NavParams } from '@ionic/angular';
import { ItemDTO } from 'src/app/models/fichas/utils/item.dto';
import { CriarComponenteComponent } from '../../criacoes/criar-componente/criar-componente.component';
import { CriarItemService } from 'src/app/services/criacoes/criar-item.service';
import { convertToItem, convertObjToItem, InventarioDTO } from 'src/app/models/fichas/utils/inventario.dto';
import { tipoITEM, tipoUTILIZAVEL } from 'src/app/config/rpg.config';
import { EquiparItemService } from 'src/app/services/gameplay/equipar-item.service';
import { InventarioService } from 'src/app/services/inventario/inventario.service';
import { TipoItem } from '../../../config/rpg.config'
import { ItemService } from 'src/app/services/criacoes/item/item.service';
import { InventarioUpdateDTO } from 'src/app/models/inventario/inventario-update.dto';
import { ItemViewDTO } from 'src/app/models/fichas/utils/item-view.dto';
import { MessageService } from 'primeng/api';
import { PresentAtaqueComponent } from '../present-ataque/present-ataque.component';
import { PresentDefesaComponent } from '../present-defesa/present-defesa.component';
import { PresentUtilizavelComponent } from '../present-utilizavel/present-utilizavel.component';


@Component({
  selector: 'app-inventario-view',
  templateUrl: './inventario-view.component.html',
  styleUrls: ['./inventario-view.component.scss'],
})
export class InventarioViewComponent implements OnInit {

  loading = false;

  inventario: InventarioDTO = {
    idInventario: null,
  };

  @Input('idInventario')
  idInventario: string = '';

  items: ItemDTO[] = [];
  rowSelected: HTMLElement;
  itemSelected: ItemViewDTO;

  defaultImg: string = "assets/imgs/battle-gear.png";
  imgTemplate: string = this.defaultImg;

  acrescimoDesconto: number = 0;
  tipoItem = TipoItem;
  selectedItem = -1;
  inventarioUP: InventarioUpdateDTO;
  abaAtual = 0;

  selectedItemView: ItemViewDTO;

  peso: number;

  constructor(public modalCtrl: ModalController,
    public alertController: AlertController,
    private itemService: CriarItemService,
    private equiparService: EquiparItemService,
    private inventarioService: InventarioService,
    private itensService: ItemService,
    private toast: MessageService) {

  }

  ngOnInit() {
    this.loading = true;
    this.inventarioService.findById(this.idInventario)
      .subscribe(res => {
        this.inventario = {
          idInventario: res.idInventario,
          dinheiro: res.dinheiro,
          itensAtaque: res.itensAtaque,
          itensDefesa: res.itensDefesa,
          itensUtilizavel: res.itensUtilizavel
        }
        console.log('inv: ', this.inventario)
        this.rowSelected = document.createElement("b");
        this.updateTable();
        this.getPesoTotal();
        this.loading = false;
      },
        error => {
          console.log(error);
          this.loading = false;
        });
  }

  updateTable() {
    switch (this.abaAtual) {
      case 1:
        this.items = this.inventario.itensDefesa;
        break;

      case 2:
        this.items = this.inventario.itensUtilizavel;
        break;

      default:
        this.items = this.inventario.itensAtaque;
    }

  }

  setList(op: number, botao, botoes) {
    botoes.forEach(botao => {
      botao.attributes.fill.nodeValue = "solid"
    });
    botao.attributes.fill.nodeValue = "outline"

    switch (op) {
      case 0:
        this.items = this.inventario.itensAtaque;
        this.abaAtual = 0;
        break;
      case 1:
        this.items = this.inventario.itensDefesa;
        this.abaAtual = 1;
        break;
      case 2:
        this.items = this.inventario.itensUtilizavel;
        this.abaAtual = 2;
        break;
    }

  }

  selecionar(item: ItemDTO, row: HTMLElement) {
    this.rowSelected.classList.remove('selected');
    row.classList.add('selected');

    this.rowSelected = row;
    this.itemSelected = item;

    if (!this.itemSelected.imgURL) {
      this.imgTemplate = this.defaultImg;
    } else {
      this.imgTemplate = this.itemSelected.imgURL;
    }
  }

  getPesoTotal() {
    this.peso = 0;
    this.inventario.itensAtaque.forEach(item => {
      this.peso += item.peso;
    });

    this.inventario.itensDefesa.forEach(item => {
      this.peso += item.peso;
    });

    this.inventario.itensUtilizavel.forEach(item => {
      this.peso += item.peso;
    });

  }


  setTipoitem(event) {
    this.selectedItem = event.detail.value;
  }



  getEnum(value): number {
    let n = 0;
    switch (value) {
      case 'ATAQUE':
        n = 0;
        break;

      case 'DEFESA':
        n = 1;
        break;

      case 'UTILIZAVEL':
        n = 2;
        break;

    }

    return n;
  }

  callToast() {
    this.toast.add({
      severity: 'warn',
      summary: 'Em breve',
      detail: 'Funcionalidade ainda não disponível'
    });
  }

  callGenericToast(severity, summary, detail) {
    this.toast.add({
      severity,
      summary,
      detail,
    });
  }

  async detalhar() {
    const { tipoItem } = this.itemSelected;
    let component;

    switch (tipoItem.toString()) {
      case 'ATAQUE':
        component = PresentAtaqueComponent;
        break;

      case 'DEFESA':
        component = PresentDefesaComponent;
        break;

      case 'UTILIZAVEL':
        component = PresentUtilizavelComponent;
        break;
    }

    const modal = await this.modalCtrl.create({
      component,
      componentProps: {
        item: this.itemSelected,
      },
      backdropDismiss: false

    });

    return await modal.present();
  }

}


